import React, { Component } from 'react'
import styled from 'styled-components'

const StyledPara = styled.p`
  color: grey;
`

class ColumnTally extends Component {
  render() {
    const cart = this.props.cart
    const itemsToRender = cart.filter(cartItem => cartItem.confirmationStatus)
    const filt = itemsToRender.length

    return (
      <div>
        <StyledPara>{filt} of</StyledPara>
        {/* {cart.map((cartItem, index) => {
          return <p key={index}>{cartItem.name}</p>
        })} */}
      </div>
    )
  }
}

export default ColumnTally
